td {
  vertical-align: top;
  padding-bottom: 15px;
}

hr {
  height: 1px;
  color: #999;
  background-color: #999;
  margin: 30px 0;
}

select {
  color: #999 !important;
  background: transparent;
}

input[type="text"]::placeholder {
  color: #d9d9d9;
}

input[type="password"]::placeholder {
  color: #d9d9d9;
}

input {
  height: 50px;
}

.row > * {
  padding: 0;
}

.form-select {
  height: 50px;
}

.text-area {
  padding: 20px;
}

.empty {
  color: #999;
}

.form-body {
  height: calc(100vh - 80px);
  overflow-y: auto;
}

.form-container {
  margin: auto;
  padding: 20px;
  padding-bottom: 50px;
  max-width: 500px;
  display: flex;
  flex-direction: column;
}
.formChild {
  overflow-y: scroll;
  scroll-behavior: smooth;
  /* height: 190px; */
}
.form-head {
  color: #6c54a6;
  font-size: 20px;
  font-weight: bold;
  margin-top: 30px;
  margin-bottom: 5px;
}
.formChild::-webkit-scrollbar {
  display: none;
}
.form-desc {
  color: #d9d9d9;
  margin-bottom: 30px;
}

.label-desc {
  color: #aeaab4;
  margin: 10px 5px;
}

.form-summary {
  color: #444444;
  /* margin-bottom: 30px; */
}

.form-control {
  margin-bottom: 10px;
}

.save-btn {
  background-color: var(--color_p2);
  border: var(--color_p2) 2px solid;
  /* width: 273px; */
  height: 50px;
  color: #fff;
  border-radius: 5px;
}

.save-btn:hover {
  background-color: #fff;
  border: var(--color_p2) 2px solid;
  color: var(--color_p2);
}

.cancel-btn {
  background-color: #aeaab4;
  border: #aeaab4 2px solid;
  /* width: 152px; */
  height: 50px;
  color: #fff;
  border-radius: 5px;
  /* margin-right: 28px !important; */

}

.cancel-btn:hover {
  background-color: #fff;
  border: #aeaab4 2px solid;
  color: #aeaab4;
}

.form-toggle {
  padding: 0px 30px;
  margin-bottom: 10px;
  border-bottom: 1px solid #d8d8d8;
  overflow-x: auto;
}
.form-toggle::-webkit-scrollbar{
  display: none;
}

.toggle-option {
  /* min-width: 50%; */
  padding: 7px 10px;
  color: #d8d8d8;
  font-weight: bold;
  cursor: pointer;
  margin-right: 103px;
  font-size: 16px;
  /* width: 150px; */
}

.active-toggle {
  color: #6c54a6;
  border-bottom: 4px solid #8443e5;
}

.small-text {
  color: #444444;
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 10px;
}

.full-width-btn {
  color: #fff;
  background-color: #6c54a6;
  width: 100%;
  height: 50px;
  border: none;
  border-radius: 3px;
  padding: 0 20px;
  margin: 10px 0 20px;
}

/* Roles0 */
.role_boxes {
  margin: 12px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.role_boxes input {
  width: 100%;
  height: 44px;
  border-radius: 6px;
  border: solid 1px #aaaeb8;
  background-color: #fff;
  color: #444;
  font-size: 16px;
  outline: none;
  padding: 17px 28px;
}
.role_boxes svg {
  font-size: 25px;
  width: 30px;
  margin-left: 10px;
  color: #a8a8a8;
}

.summary-head {
  color: var(--color_p2);
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 15px;
}

.summary-head span {
  margin-left: 20px;
}

.summary-info {
  margin-top: 30px;
}

.summary-name {
  color: #aeaab4;
  padding-right: 30px;
}

.photoUpload {
  width: 300px;
}
.charTable {
  width: 100%;
}
.charTable > table,
th,
td {
  border: 1px solid black;
  border-collapse: collapse;
  text-align: center;
}

.form_group {
  position: absolute;
  top: -10px;
  left: -130px;
  transform: rotate(-90deg) translate(-50%, -50%);
  width: 230px;
  z-index: -99;
}

.form_group p {
  color: #b6b6b6;
  font-size: 12px;
  margin: 0;
  background-color: white;
  padding: 0 10px;
  position: absolute;
  left: 0;
  right: 0;
  top: 0px;
  margin: auto;
  width: fit-content;
}

.form_group img {
  width: 100%;
}

.summary-info.row div span {
  display: inline-block;
}

.summary-info.row div {
  margin-bottom: 16px;
}
.summary-info.row div span.titleSpan {
  margin-right: 26px;
  width: 76px;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #aeaab4;
}

.summary-info.row div span.contentSpan {
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.71;
  letter-spacing: normal;
  text-align: left;
  color: #444;
}

#finput {
  padding-right: 20px;
  padding-top: 20px;
}
